___CSS_LOADER_EXPORT___.push([module.id, "\n.placeResource{\n  width: 100%;\n  height: 100%;\n  position: fixed;\n  background-color: #101720;\n}\n.placeResource-content{\npadding: 0px 3% 52% 3%;\n  width: 100%;\n  height: 80%;\n  position: fixed;\n  overflow: scroll;\n}\n.placeResource-list-card{\n  margin-top: 3%;\n}\n.van-search__content {\n  background-color: #1A212B;\n}\ninput::-webkit-input-placeholder{\n  color: #373F4A !important;\n\nfont-size: 12px\n}\n.van-field__control{\n  color: #8BA3C2;\n}\n", "",{"version":3,"sources":["webpack://./src/views/placeResourse/placeResourse.vue"],"names":[],"mappings":";AA2FA;EACA,WAAA;EACA,YAAA;EACA,eAAA;EACA,yBAAA;AACA;AACA;AACA,sBAAA;EACA,WAAA;EACA,WAAA;EACA,eAAA;EACA,gBAAA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA,yBAAA;AAEA;AACA;EACA,yBAAA;;AAEA;AACA;AACA;EACA,cAAA;AACA","sourcesContent":["<template>\n  <div class=\"placeResource\">\n    <van-search v-model=\"queryCondition\" placeholder=\"网点名称/网点地址\" background=\"#101720\" @search=\"onSearch\" />\n    <div class=\"placeResource-content\">\n      <van-loading v-if=\"!placeResourcList\" size=\"24px\" vertical>\n        加载中...\n      </van-loading>\n      <van-list\n        v-model=\"loading\"\n        :finished=\"finished\"\n        finished-text=\"没有更多了\"\n        class=\"van-clearfix\"\n        :immediate-check=\"false\"\n        @load=\"getPlaceResourcList\"\n      >\n        <Adaptive v-for=\"item in placeResourcList\" :key=\"item.index\" :data=\"['94%','31.39%']\" class=\"placeResource-list-card\">\n          <PlaceResourcListCard :data=\"item\" />\n        </Adaptive>\n      </van-list>\n    </div>\n  </div>\n</template>\n\n<script>\nimport PlaceResourcListCard from 'cmp/placeResourcListCard/PlaceResourcListCard'\nimport ReadTypeNameOnVuex from '@/utils/readTypeNameOnVuex'\n\nimport Api from '@/api/placeResource/placeResource.js'\n\nexport default {\n  components: {\n    PlaceResourcListCard\n  },\n  data() {\n    return {\n      loading: false,\n      placeResourcList: [],\n      queryCondition: '',\n      placeTypeList: [],\n      page: 0,\n      finished: false\n    }\n  },\n  mounted() {\n    if (this.placeResourcList.length === 0) {\n      this.getPlaceResourcList()\n    }\n  },\n  methods: {\n    async getPlaceResourcList() {\n      const params = {\n        // systemType: this.thisSubsystemId,\n        page: ++this.page,\n        size: 10,\n        condition: (this.queryCondition.length < 1 ? '' : ('?condition=' + this.queryCondition))\n      }\n      const res = await Api.placeResourcList(params)\n      console.log('输出长度', res)\n\n      if (res.code === 200) {\n        let listData = [...res.data.rows]\n\n        if (listData.length === 0) {\n          this.loading = false\n          this.finished = true\n          this.placeResourcList = []\n          return\n        }\n        // 去vuex获取该网点的网点类型名称，放到数组集合里\n        listData = await ReadTypeNameOnVuex.conversion('placeType', 'placeTypeId', 'placeTypeName', listData)\n        this.placeResourcList = this.placeResourcList.concat(listData)\n      }\n      if (params.page === res.data.total) {\n        this.finished = true\n      }\n      this.loading = false\n    },\n    /**\n     * 搜索触发事件\n     */\n    onSearch(e) {\n      this.placeResourcList = []\n      this.finished = false\n      this.page = 0\n      this.getPlaceResourcList()\n    }\n  }\n}\n</script>\n\n<style>\n.placeResource{\n  width: 100%;\n  height: 100%;\n  position: fixed;\n  background-color: #101720;\n}\n.placeResource-content{\npadding: 0px 3% 52% 3%;\n  width: 100%;\n  height: 80%;\n  position: fixed;\n  overflow: scroll;\n}\n.placeResource-list-card{\n  margin-top: 3%;\n}\n.van-search__content {\n  background-color: #1A212B;\n\n}\ninput::-webkit-input-placeholder{\n  color: #373F4A !important;\n\nfont-size: 12px\n}\n.van-field__control{\n  color: #8BA3C2;\n}\n</style>\n"],"sourceRoot":""}]);
