istCard-type\">\n            {{ data.placeTypeName }}\n          </div>\n        </div>\n        <a @click.stop=\"callPhone(data.placeId)\">\n          <img src=\"@/assets/images/equip/phone.png\" alt=\"\" class=\"placeResourcListCard-content-row-icon\">\n        </a>\n      </div>\n      <div class=\"placeResourcListCard-content-row\">\n        <div class=\"placeResourcListCard-content-row-name\">\n          <!-- {{ data.placeName }} -->\n          <span class=\"placeResourcListCard-content-row-adress-name\">\n            {{ data.placeAddress }}\n          </span>\n        </div>\n        <a @click.stop=\"toMap(data.placeId)\">\n          <img src=\"@/assets/images/equip/navigation.png\" alt=\"\" class=\"placeResourcListCard-content-row-icon\">\n        </a>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport Api from '@/api/placeResource/placeResource'\n\nexport default {\n  components: {\n    // EquipStatus\n  },\n  props: {\n    data: {\n      type: Object,\n      // eslint-disable-next-line vue/require-valid-default-prop\n      default: {}\n    }\n  },\n\n  data() {\n    return {\n      equipStatus: {\n        wifi: 80,\n        electricity: 80,\n        statusName: '正常'\n      }\n    }\n  },\n  methods: {\n    /**\n     * 跳转地图\n     */\n    toMap() {\n\n    },\n\n    /**\n     * 跳转拨号\n     */\n    async  callPhone(e) {\n      const res = await Api.placeResourcDetail(e)\n      if (res.code === 200) {\n        const result = res.data\n        window.location.href = 'tel://' + result.phone\n      }\n    },\n    /**\n     * 跳转页面\n     */\n    goJump(id) {\n      this.$router.push({\n        path: '/placeResourcDetail',\n        query: {\n          placeId: id,\n          placeTypeName: this.data.placeTypeName\n        }\n      })\n    }\n  }\n}\n</script>\n\n<style>\n.placeResourcListCard{\n  width: 100%;\n  height: 100%;\n  border: 1px solid #4D628F;\n  background-color:#0A0B29;\n}\n.placeResourcListCard-header{\n  background: #131F3B;\n}\n.placeResourcListCard-title{\n  width: 35%;\n  height: 25%;\n  font-size: 15px;\n  font-family: PingFang SC;\n  font-weight: 400;\n  color: #FFFFFF;\n  padding: 1% 5% 1% 3%;\n  margin-top: 1%;\n  display: inline-block;\n  float: left;\n}\n.placeResourcListCard-content{\n  width: 100%;\n  height: 55%;\n  margin-top: 2.7%;\n}\n.placeResourcListCard-equip-count{\n  width: 50%;\n  height: 25%;\n  font-size: 12px;\n  display: inline-block;\n  font-family: PingFang SC;\n  font-weight: 400;\n  vertical-align: middle;\n  color: #B9CEE9;\n  padding: 2.1% 1% 1% 3%;\n  text-align: right;\n  padding-right: 3%;\n}\n.placeResourcListCard-equip-count-num{\n  width: auto;\n  height: 100%;\n  display: inline-block;\n  color: #06F0FE;\n}\n.placeResourcListCard-content-row{\n  height: 45.5%;\n  width: 100%;\n  font-size: 12px;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  vertical-align: middle\n}\n.placeResourcListCard-content-row-name{\n  width: 89%;\n  text-align: left;\n  padding-left: 3%;\n  font-size: 12px;\n  font-family: PingFang SC;\n  font-weight: 400;\n  color: #B9CEE9;\n  display: flex;\n  align-items: center;\n  white-space: nowrap;\n  overflow: hidden;\n}\n.placeResourcListCard-content-row-adress-name{\n  font-size: 12px;\n  font-family: PingFang SC;\n  font-weight: 400;\n}\n.placeResourcListCard-type{\n  width: auto;\n  background: #1B2B29;\n  border: 1px solid #54A4E7;\n  border-radius: 2px;\n  color: #55A4E7;\n  font-size: 10px;\n  font-family: PingFang SC;\n  font-weight: 400;\n  color: #55A4E7;\n  padding: 0.5% 1% 0.5% 1%;\n  margin-left: 2%;\n  display: inline-block;\n  white-space: nowrap;\n  overflow: hidden;\n}\n.placeResourcListCard-phone{\n  width: auto;\n  padding: 0.5% 1% 0.5% 1%;\n  margin-left: 2%;\n  color: #B9CEE9;\n  display: inline-block;\n  white-space: nowrap;\n  overflow: hidden;\n}\n.placeResourcListCard-content-row-icon{\n  width: 19px;\n  height: 19px;\n}\n</style>\n"],"sourceRoot":""}]);
